 a {
  color: rgba(154,255, 206,0.9);
  text-decoration: none;
 }

 .heading {
  position: relative;
  font-family: 'Raleway', sans-serif;
  text-transform: uppercase;
  text-align: center;
  font-weight: 100;
  font-size: calc(5px + 4vw);
  width: 50%;
  margin: 0 auto;
  /*top: 50px;*/
  padding: 0;
 }

.comp-head {
  position: relative;
  background-image: url("comp-head-1326px.jpg");
  background-size: cover;
  background-position: center bottom;
  height: 100vh;
}

 .main-head {
  position: relative;
  font-size: calc(5px + 4vw);
  margin: 0 calc((100% - 50%) / 2) ;
  top: calc(33vh - 6vw);
 }

 .server-rack {
  position: relative;
  background-color: #14191c;
  color: #9affce;
  padding: 20px 10px;
  margin: 0;
  z-index: 1;
 }

.cord {
  width:100%;
  text-align: center;
  padding: 0;
  margin: 0;
 }
.cord-image {
  width:125px;
}

.skill-list {
  position: relative;
  background-color: #14191c;
  z-index: 2;
}
 .skillbox {
  min-height: 60px;
  max-width: 90%;
  width: 500px;
  border: 1px solid #9affce;
  border-radius: 10px;
  margin: 20px auto;
  position: relative;
  background: -webkit-radial-gradient( circle farthest-side, rgba(20,25,28,1) 85%, rgba(24,36,37,1));
  background: -o-radial-gradient( circle farthest-side, rgba(20,25,28,1) 85%, rgba(24,36,37,1));
  background: radial-gradient( circle farthest-side, rgba(20,25,28,1) 85%, rgba(24,36,37,1));
 }

 .skill-head {
  position: static;
  font-size: 35px;
  width: 100%;
  margin: 7px 0 0 0;
 }

 .description {
font-family: 'Raleway', sans-serif;
padding: 20px;
margin: 20px;
color: white;
font-weight: 100;
/*border: 1px solid white;*/
border-radius: 10px;
 }

 @media screen and (max-width: 450px) {
  .skill-head {
  font-size: 22px;
  }

  .skillbox {
  min-height: 45px;
 }
}
 .who-am-i {
  background-image: url("me-comp-1360px.jpg");
  background-size: cover;
  background-position: center;
  height: 160vw;
 }

 .who-head {
  font-size: calc(20px + 4vw);
 }

.who-content {
  position: relative;
  top: 30vw;

  font-family: 'Raleway', sans-serif;
  font-size: calc(2px + 3vw) ;
  font-weight: 100;
}

.who-para {
  margin:0;
    max-width: 600px;
  margin-right: 10%;
  margin-left: 35%;
}

@media screen and (max-width: 800px) {
  .who-para {
    font-weight: 300;

  }
}

 .footer-line {
  border-top: 1px solid #9affce;
 }

 .circle-left {
  border: 1px solid #9affce;
  border-top-left-radius: 160px;
  border-bottom-left-radius: 160px;
  border-right: 0;
}

.circle-right {
  border: 1px solid #9affce;
  border-top-right-radius: 160px;
  border-bottom-right-radius: 160px;
  border-left: 0;
}

 .inner {
  width: 49px;
  height: 45px;
  position: relative;
  top: 18px;
  left: 18px;
 }

  .outer {
  width: 85px;
  height: 85px;
  position: absolute;
  bottom: -54px;
  z-index: -1;
 }

 .left {
  left: -30px;
 }

 .right {
  right: -30px;
 }

 @media screen and (max-width: 450px) {
   .outer {
  width: 70px;
  height: 70px;
  position: absolute;
  bottom: -47px;
  z-index: -1;
 }

  .inner {
  width: 52px;
  height: 50px;
  position: relative;
  top: 9px;
  left: 9px;
 }
 .left {
  left: -20px;
 }

 .right {
  right: -20px;
 }


 }

 .project {
  padding: 10px;
  border-radius: 10px;
  text-align: center;
  border: 1px solid rgba(100,206,255,0.1);
  margin-bottom: 20px;
  /*background: linear-gradient(rgba(255,255,255,0) 95%, rgba(154,255,206,0.3));*/
  background: -webkit-radial-gradient(rgba(255,255,255,0) 90%, rgba(100,255,206,0.2));
  background: -o-radial-gradient(rgba(255,255,255,0) 90%, rgba(100,255,206,0.2));
  background: radial-gradient(rgba(255,255,255,0) 90%, rgba(100,255,206,0.2));
 }

 .project-heading {
  text-transform: uppercase;
  font-weight: 100;
  font-size: 30px;
  margin: 10px;
  text-decoration: none;
 }

.project-image {
  display: block;
  margin: 0 auto;
  max-width: 100%;
  border-radius: 5px;
}

.skill-button {
  margin: 5px;
  display: inline-block;
  border: 1px solid rgba(255,255,255,0.4);
  padding: 5px;
  border-radius: 5px;
  font-size: 14px;
}

.project-links {
  margin: 5px;
  display: inline-block;
  text-decoration: underline;
}

.contact {
  padding: 50px;
  border-radius: 400px;
  height: 250px;
  width: 250px;
  max-width: 100%;
  border: 1px solid #9affce;
  text-align: center;
  color: white;
    font-family: 'Raleway', sans-serif;
    font-weight: 300;
    max-width: 400px;
    margin: 20px auto;
}

@media screen and (max-width: 400px) {
  .contact {
    padding: 20px;
  }
}
.logo {
  width: 60px;
  margin: 20px;
}

.container-fluid {
  width: 100%;
  overflow: hidden;
}

.close-but {
  text-align: right;
  margin: 0;
  font-size: 14px;

}


